

.card {
    position: relative;
}

.card img {
    width: 100%;
    display: block;
    
    border-radius: 6px;
}

.card .front {
    transform: rotateY(90deg);
    transition: all ease-in 0.1s;
    position: absolute
}

.flipped .front {
    transform: rotateY(0deg);
}

.card .back {
    transition: all ease-in 0.1s;
    transition-delay: 0.1s;
}

.flipped .back {
    transform: rotateY(90deg);
    transition-delay: 0s;
}